.create-group-main-div{
    background: #FFFFFF;
    border: 1px solid #ECECEC;
    box-sizing: border-box;
    border-radius: 16px;
    width: 50%;
    margin: auto;
    margin-top: 128px;
    padding: 36px 0;
}
.create-group-div{
    width: 85%;
    margin: auto;
}
.create-group-title{
    font-family: IRANSans;
    font-weight: bold;
    font-size: 22px;
    line-height: 38px;
    color: #121921;
    text-align: center;
}
.create-group-div-text{
    font-family: IRANSans;
    font-size: 18px;
    line-height: 16px;
    color: #383F48;
    text-align: right;
    font-weight: 300;
    margin-top: 24px;
}
.create-group-div input{
    border: 0.5px solid #ECECEC;
    box-sizing: border-box;
    border-radius: 8px;
    width: 100%;
    padding:15px 10px;
}
.create-group-toggle-div{
    width: 100%;
    background-color: #F9FAFB;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 24px;
    cursor: pointer;
}
.create-group-toggle-active-right-div{
    background: #FFFFFF;
    border: 0.5px solid #ECECEC;
    box-sizing: border-box;
    border-radius: 8px;
    width: 50%;
    padding: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
}
.create-group-toggle-deactive-right-div{
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
}
.create-group-toggle-active-left-div{
    background: #FFFFFF;
    border: 0.5px solid #ECECEC;
    box-sizing: border-box;
    border-radius: 8px;
    width: 50%;
    padding: 8px;
    direction: ltr;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
}
.create-group-toggle-deactive-left-div{
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    direction: ltr;
    z-index: 1;
}
.create-group-toggle-active-text{
    font-family: IRANSans;
    font-size: 14px;
    line-height: 16px;
    color: #519AFF;
    text-align: center;
    font-weight: 300;
}
.create-group-toggle-deactive-text{
    font-family: IRANSans;
    font-size: 14px;
    line-height: 16px;
    color: #4F4F4F;
    text-align: center;
    font-weight: 200;
}
.create-group-div button{
    font-family: IRANSans;
    font-size: 24px;
    line-height: 41px;
    color: #ffffff;
    font-weight: 200;
    width: 60%;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 8px 0;
    background-color: #519AFF;
    border-radius: 8px;
    border: none;
    margin-top: 36px;
    cursor: pointer;
}