.landing-sigmoid-main-div{
    background-color: #E4F3FF;
    display: flex;
    align-items: center;
    justify-content: center; 
    flex-direction: column;
    border-bottom-right-radius: 1000%;
    border-bottom-left-radius: 1000%;
}
.landing-sigmoid-title{
    font-size: 30px;
    font-weight: 500;
    font-family: IRANSans;
    line-height: 51px;
    color: #333333;
}
.landing-sigmoid-text{
    font-size: 18px;
    font-weight: 300;
    font-family: IRANSans;
    line-height: 32px;
    color: #333333;
    margin: 0;
}