.footer-main-div{
    background: linear-gradient(180deg, #383F48 10.61%, #121921 79.65%);
    width: 100%;
    padding-top: 40px;
    padding-bottom: 56px;
}
.footer-div{
    width: 80%;
    margin: auto;
}
.footer-social-div{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.footer-site-name{
    font-size: 20px;
    font-family: Gugi;
    line-height: 24px;
    color: #ffffff;
    font-weight: normal;
    text-align: right;
}
.footer-social-pic img{
    margin-right: 24px;
    cursor: pointer;
}
.footer-social-pic img:hover{
    fill: #828282;
}
.footer-contact-div{
    display: flex;
    align-items: center;
    flex-direction: row;
}
.footer-contact-div p{
    font-family: IRANSans;
    font-weight: 500;
    font-size: 18px;
    line-height: 24px;
    color: #ffffff;
    cursor: pointer;
    margin-left: 36px;
}
.footer-contact-div img{
    margin-right: 24px;
}
.footer-detail-text{
    font-family: IRANSans;
    font-weight: 200;
    font-size: 14px;
    line-height: 24px;
    color: #ffffff;
    opacity: .5; 
    margin-top: 8px;
}