.toolbar-pic{
    background-image: url(../../assets/icons/homepageBG.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    max-height: 1024px;
    min-height: 1000px;
    top: 0;
    left: 0;
    right: 0;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.viewOnImg{
    width: 100%;
    height: 100%;
    background: #121921;
}
.toolbar-main-title{
    font-size: 28px;
    color: #ffffff;
    font-weight: bold;
    font-family: IRANSans;
    line-height: 47px;
}
.toolbar-text-first-row{
    font-size: 20px;
    color: #ffffff;
    font-weight: 300;
    font-family: IRANSans;
    line-height: 34px;
    width: 52%;
}
.toolbar-text-second-row{
    font-size: 20px;
    color: #ffffff;
    font-weight: 300;
    font-family: IRANSans;
    line-height: 34px;
    margin: -5px;
    width: 45%;
}
.toolbar-button{
    background-color: #519AFF;
    border-radius: 32px;
    color: #ffffff;
    font-weight: 500;
    font-family: IRANSans;
    font-size: 24px;
    line-height: 41px;
    cursor: pointer;
    padding: 5px 60px;
    position: absolute;
    bottom: 100px;
    border: none;
}
