.signup-main-div{
    width: 100%;
    display: grid;
    grid-template-columns: 62% 38%;
    padding: 75px 0;
}
.signup-right-div{
    width: 100%;
    background-color: #F0F0F0;
    height: 99.45%;
}
.signup-main-title{
    font-family: IRANSans;
    font-size: 30px;
    font-weight: 900;
    color: #86C4D1;
    text-align: center;
    line-height: 16px;
    cursor: pointer;
}
.signup-button{
    width: 25%;
    margin: auto;
    background-color: #86c4d1;
    border-radius: 8px;
    color: #ffffff;
    font-family: IRANSans;
    font-size: 20px;
    font-weight: 500;
    text-align: center;
    line-height: 34px;
    border: none;
    padding: 8px 0;
    cursor: pointer;
    display: flex;
    justify-content: center;
    margin-top: 40px;
}
.signup-rule-div{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 24px;
}
.signup-rule-div p{
    font-family: IRANSans;
    font-size: 12px;
    font-weight: 200;
    color: #828282;
    text-align: center;
    line-height: 20px;
    margin-left: 5px;
}
.signup-input-div{
    width: 100%;
    display: grid;
    grid-template-columns: 15% 25% 20% 25% 15%;
}
.signup-input-div input{
    width: 100%;
    border: none;
    border-bottom:  0.5px solid #d6d6d6;
    padding: 8px 5px;
    font-size: 16px;
    font-weight: normal;
    color: #828282;
    text-align: right;
    line-height: 27px;
    margin-top: 35px;
    background-color: transparent;
    border-radius: 10px;
    outline: none;
}

.signup-input-div input:focus{
    box-shadow: 0px 0px 3px #c4c4c4;
}

.signup-input-div .errors{
    width: 100%;
    margin-top: -8px ;
    padding-right: 8px;
    height: 20px;
    display: block;
    position: relative;
}
.signup-input-div .errors p{
    font-size: 12px;
    display: block;
    position: relative;
    color: red;
}

.signup-input-div .forms{
    width: 100%;
}

.signup-captcha-div{
    display: flex;
    align-items: center;
    direction: ltr;
    margin-top: 35px;
}
.signup-captcha-div img{
    cursor: pointer;
    margin-right: 10px;
}
.signup-captcha-div p{
    font-family: IRANSans;
    font-size: 22px;
    font-weight: bold;
    color: #BDBDBD;
    text-align: center;
    line-height: 20px;
    margin-left: 5px;
}
.signup-div{
    background-color: #121921;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.imgs{
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    max-height: 650px;
    min-height: 630px;
    top: 0;
    left: 0;
    right: 0;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
