.landing-slider-main-div{
    width: 100%;
    background-color: #ffffff;
    padding: 40px 0;
    margin-top: 104px;
}
.landing-slider-div{
    width: 80%;
    margin: auto;
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
}
.landing-slider-div div{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.landing-slider-div p{
    font-family: IRANSans;
    font-weight: normal;
    font-size: 18px;
    color: #333333;
    text-align: center;
    margin-top: 80px;
}