.above-footer-main-div{
    background: linear-gradient(180deg, rgba(139, 213, 244, 0) 0%, rgba(139, 213, 244, 0.25) 97.19%);
    display: grid;
    grid-template-columns: 30% 5% 40% 25%;
}
.above-footer-main-div p{
    font-style: normal;
    font-weight: 200;
    font-size: 24px;
    line-height: 41px;
    color: #8BD5F4;
    display: flex;
    align-items: center;
}
