.toolbar-main-div{
    display: flex;
    flex-direction: column;
    border: 0.5px solid #EFF4F5;
    box-sizing: border-box;
    border-radius: 8px;
    width: 100%;
    margin-top: 180px;
    margin-bottom: 230px;
}
.toolbar-avatar-div{
    display: flex;
    align-items: center;
    text-align: right;
    margin: auto;
    margin-right: 8px;
    margin-top: 24px;
}
.toolbar-avatar-div p{
    font-family: IRANSans;
    font-weight: 500;
    font-size: 20px;
    line-height: 34px;
    color: #333333;
    text-align: right;
    margin-right: 16px;
}
.toolbar-credit-div{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 85%;
    margin-right: 0;
    margin-top: 16px;
}
.toolbar-credit-div p{
    font-family: IRANSans;
    font-weight: 300;
    font-size: 16px;
    line-height: 27px;
    color: #2F80ED;
    text-align: right;
    margin: auto;
    margin-right: 8px;
}
