
.header-main-div{
    width: 80%;
    background-color: transparent;
    margin: auto;
    top: 4%;
    right: 0;
    left: 0;
    z-index: 1;
    position: absolute;
    display: grid;
    grid-template-columns: 10% 25% 40% 10% 15%;
}

.header-toolbar-div{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.header-toolbar-link{
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: #ffffff;
    padding: 5px 5px;
    text-decoration: none;
}
.header-toolbar-link:hover{
    color: #2F80ED;
    border-bottom: 1px solid #2F80ED;

}
.header-toolbar-enter-link{
    text-decoration: none;
}
.header-toolbar-enter-button{
    background-color: #61d9ff; /* Green */
    border: none;
    color: white;
    border-radius: 30px;
    padding: 5px 30px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    cursor: pointer;
}
.logout-button{
    background-color: #ff3f3f; /* Green */
    border: none;
    color: white;
    border-radius: 30px;
    padding: 5px 30px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    cursor: pointer;
}


.header-toolbar-enter-link:hover{
    color: #14B47A;
    cursor: pointer;
}
.header-toolbar-exit-link:hover{
    color:#F54545;
}
