.modal-main-div{
    width: 100%;
    height: 137%;
    z-index: 1;
    background: rgba(51, 51, 51, 0.96);
    backdrop-filter: blur(6px); 
    top: 0;
    left: 0;
    right: 0;
    position: absolute;
}
.modal-header-div{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 75%;
    margin: auto;
    margin-top: 84px;
}
.modal-header-div p{
    font-family: IRANSans;
    font-weight: 500;
    color: #B1ECFF;
    font-size: 26px;
    line-height: 44px;
    text-align: right;
}
.modal-header-div button{
    cursor: pointer;
    background-color: transparent;
    border: none;
    margin-top: -15px;
}
.modal-text-div{
    display: flex;
    flex-direction: column;
    width: 75%;
    margin: auto;
    margin-top: 32px;
}
.modal-text-div p{
    font-family: IRANSans;
    font-weight: 300;
    color: #ffffff;
    font-size: 20px;
    line-height: 44px;
    text-align: right;
    margin-top:5px;
}