.landing-chat-title{
    font-family: IRANSans;
    font-weight: bold;
    font-size: 30px;
    line-height: 51px;
    text-align: center;
    color: #333333;
}
.landing-chat-text{
    font-family: IRANSans;
    font-weight: 400;
    font-size: 18px;
    line-height: 31px;
    text-align: center;
    color: #333333;
    margin-top: 64px;
}
.landing-chat-image-div{
    display: flex;
    justify-content: center;
    align-items: center;
}
.landing-chat-div{
    width: 100%;
    background-image: url(../../assets/icons/ChatBg.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    display: flex;
    align-items: center;
    display: flex;
    flex-direction: column;
    padding: 160px 0;
}
.landing-chat-div p{
    font-family: IRANSans;
    font-weight: 500;
    font-size: 22px;
    line-height: 37px;
    text-align: center;
    color: #ffffff;
    margin: 0;
}
