.signin-main-div{
    width: 100%;
    display: grid;
    grid-template-columns: 62% 38%;
    padding: 75px 0;
}
.signin-right-div{
    background-color: #F0F0F0;
    height: 99.45%;
}
.signin-button{
    width: 40%;
    margin: auto;
    background-color: #86C4D1;
    border-radius: 8px;
    color: #ffffff;
    font-family: IRANSans;
    font-size: 20px;
    font-weight: 500;
    text-align: center;
    line-height: 34px;
    border: none;
    padding: 8px 0;
    cursor: pointer;
    display: flex;
    justify-content: center;
    margin-top: 64px;
}
.signin-input-div{
    width: 60%;
    margin: auto;
}
.signin-input-div input{
    width: 100%;
    border: none;
    border-bottom:  0.5px solid #E0E0E0;
    padding: 8px 5px;
    font-family: IRANSans;
    font-size: 16px;
    font-weight: normal;
    color: #828282;
    text-align: right;
    line-height: 27px;
    margin-top: 40px;
    border-radius: 10px;
    background-color: transparent;
    outline: none;
}

.signin-input-div input:focus{
    box-shadow: 0px 0px 3px #aad2d4;
}

.signin-input-div .errors{
    width: 100%;
    margin-top: -8px ;
    padding-right: 8px;
    height: 20px;
    display: block;
    position: relative;
}
.signin-input-div .errors p{
    font-size: 12px;
    display: block;
    position: relative;
    color: red;
}

.signin-title{
    font-family: IRANSans;
    font-size: 30px;
    font-weight: 900;
    color: #86C4D1;
    text-align: center;
    line-height: 16px;
    margin-top: 40px;
    cursor: pointer;
}
.signin-captcha-input-div{
    display: grid;
    grid-template-columns: 35% 35% 30%;
    margin-top: 40px;
}
.signin-captcha-div{
   display: flex;
   flex-direction: row-reverse;
   align-items: center;
}
.signin-captcha-div img{
    cursor: pointer;
    margin-right: 10px;
}
.signin-captcha-div p{
    font-family: IRANSans;
    font-size: 22px;
    font-weight: bold;
    color: #BDBDBD;
    text-align: center;
    line-height: 20px;
    margin-left: 5px;
}
.signin-div{
    background-color: #121921;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.signin-go-to-signup-text{
    font-family: IRANSans;
    font-size: 12px;
    font-weight: 200;
    color: #828282;
    text-align: center;
    line-height: 20px;
    margin-top: 8px;
    cursor: pointer;
}
.imgs{
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    max-height: 650px;
    min-height: 630px;
    top: 0;
    left: 0;
    right: 0;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
