.increase-credit-main-title{
    font-family: IRANSans;
    font-weight: 500;
    font-size: 20px;
    line-height: 34px;
    color: #383F48;
    margin: auto;
    margin-top: 250px;
}
.increase-credit-money-div{
    display: flex;
    align-items: center;
    width: 20%;
}
.increase-credit-money-div p{
    font-family: IRANSans;
    font-weight: normal;
    font-size: 12px;
    line-height: 34px;
    color:#828282;
    margin: auto;
    margin-top: 16px;
    margin-left: 4px;
}
.increase-credit-select-button-div{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: auto;
    margin-top: 90px;
}
.increase-credit-select-button-div button{
    background: #F9FAFB;
    border: 0.5px solid #ECECEC;
    box-sizing: border-box;
    border-radius: 32px;
    padding: 6px 25px;
    font-family: IRANSans;
    font-weight: 200;
    font-size: 18px;
    line-height: 31px;
    color: #BDBDBD;
    cursor: pointer;
}
.increase-credit-select-button-div button:hover{
    background-color: #C3F0FF;
    color: #4975A1;
}
.increase-credit-input-div{
    display: flex;
    align-items: center;
    margin: auto;
    margin-top: 80px;
}
.increase-credit-input-div button{
    width: 4%;
    padding: 7px 0;
    background: #ECECEC;
    border-radius: 8px;
    border: none;
    cursor: pointer;
}
.increase-credit-input-div button:hover{
    background-color: #C3F0FF;
    color: #4975A1;
}
.increase-credit-input-div div{
    width: 5%;
}
.increase-credit-input-div input{
    width: 82%;
    padding: 7px 0;
    border: none;
    border-bottom: 1px solid #ECECEC;
    background-color: #F9FAFB;
}
.increase-credit-button{
    background-color: #6AB5CF;
    border-radius: 8px;
    margin: auto;
    margin-top: 100px;
    border: none;
    box-sizing: border-box;
    padding: 7px 70px;
    font-family: IRANSans;
    font-weight: normal;
    font-size: 22px;
    line-height: 31px;
    text-align: center;
    color: #ffffff;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}